  background-color: transparent;
  cursor: pointer;
  transition: border-color 0.3s;
  outline: none; }
  .modal__close::before {
    position: absolute;
    top: 10px;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    background-color: white;
    transform: rotate(45deg);
    border-radius: 10px; }
  .modal__close::after {
    position: absolute;
    top: 10px;
    left: 0;
    content: "";
