.about {
  padding-right: 10px;
  padding-bottom: 109px;
  padding-left: 180px;
  background-image: url("../img/2005-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: 120px 317px;
  background-position: 0 4px;

  & h2 {
    @include title-h2(20px);
  }

  & p {
    margin-top: 0;
    margin-bottom: 22px;
    overflow: hidden;
    text-overflow: ellipsis;

    &:last-of-type {
      margin-bottom: 39px;
    }
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/2005-desktop@2x.jpg");
  }

  @media (max-width: $desktop-width) {
    padding-bottom: 83px;
    padding-right: 0;
    padding-left: 185px;
    background-image: url("../img/2005-tablet@1x.jpg");
    background-size: 136px 362px;
    background-position: 1px -2px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/2005-tablet@2x.jpg");
    }

    & h2 {
      margin-bottom: 26px;
    }

    & p {
      margin-bottom: 20px;
      padding-right: 10px;
      padding-left: 3px;

      &:last-of-type {
        height: 87px;
        margin-bottom: 43px;
      }
    }
  }

  @media (max-width: $tablet-width) {
    padding-bottom: 64px;
    padding-right: 0;
    padding-left: 0;
    background-image: none;

    & h2 {
      margin-bottom: 21px;
    }

    & p {
      margin-bottom: 41px;
      padding-right: 0;
      padding-left: 3px;
      overflow: hidden;
      text-overflow: ellipsis;

      &:last-of-type {
        margin-bottom: 31px;
        height: 174px;
      }
    }
  }
}

.about__text-hidden {
  @media (max-width: $desktop-width) {
    position: relative;

    &::before {
      position: absolute;
      bottom: 0;
      right: 4px;
      content: "..";
    }
  }

  @media (max-width: $tablet-width) {
    &::before {
      right: 0;
      width: 155px;
      height: 21px;
      background-color: white;
    }
  }
}

.about__more {
  @include btn-blue();

  width: 180px;
  text-align: center;

  @media (max-width: $desktop-width) {
    margin-left: 3px;
  }
}
